.contact {
    border-radius: 10px;
    width: 95%;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.473);
    margin-bottom: 10px;
    box-shadow: 0 0 10px;
}
.contact-content h2 {
    text-align: center;
    color: rgb(228, 223, 223);
    font-family: cursive;
    margin-bottom: 40px;
}
.icons {
    display: flex;
    justify-content: center;
    gap: 20px;
}
.icons a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border: 1px solid white;
    border-radius: 50%;
    font-size: 18px;
    text-decoration: none;
    transition: transform 0.3s, background-color 0.3s;
}
.linked-icon {
    color: blue;
    background-color: white;
}
.git-icon {
    color: black;
    background-color: white;
}
.mail-icon {
    color: orangered;
    background-color: white;
}
.insta-icon {
    color: rgb(138, 34, 97);
    background-color: white;
}
.icons a:hover {
    color: white;
    transform: scale(1.1);
    background-color: rgba(255, 255, 255, 0.2);
}
